    data () {
      return {
        height: document.documentElement.clientHeight
      }
    },
    mounted () {
      setTimeout(function () {
           new Hover('.jumbotron', { // eslint-disable-line
             max: 3,
             scale: 1,
             perspective: 700,
             layers: [{
               multiple: -0.04,
               reverseTranslate: true
